<template>
  <div id="ContactUs">
    <div class="banner container-fuild text-center"></div>
    <div class="container">
      <div class="container-fuild ContactUs-container">
        <div class="row">
          <div class="col-xs-12 col-sm-12 col-md-6">
            <form class="form-horizontal" role="form">
              <div class="form-group">
                <label for="name" class="col-sm-2 control-label"></label>
                <div class="col-sm-10 col-xs-12">
                  <input type="text" class="form-control" id="name" placeholder="">
                </div>
              </div>
              <div class="form-group">
                <label for="email" class="col-sm-2 control-label"></label>
                <div class="col-sm-10">
                  <input type="text" class="form-control" id="email" placeholder="">
                </div>
              </div>
              <div class="form-group">
                <label for="tel" class="col-sm-2 control-label"></label>
                <div class="col-sm-10">
                  <input type="text" class="form-control" id="tel" placeholder="">
                </div>
              </div>
              <div class="form-group">
                <label for="content" class="col-sm-2 control-label"></label>
                <div class="col-sm-10">
                  <textarea class="form-control" id="content" rows="8" placeholder=""></textarea>
                </div>
              </div>
              <div class="form-group">
                <div class="col-sm-offset-2 col-sm-10">
                  <button type="submit" class="btn btn-default btn-block" @click="CommitInfo" ></button>
                </div>
              </div>
            </form>
          </div>
          <div class="col-xs-12 col-sm-12 col-md-6">
            <div id="map" class="wow zoomIn"></div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { WOW } from 'wowjs'
import BMap from "BMap";
/*email*/
import {EMail} from "nodemailer"
export default {
  name: "ContactUs",
  data() {
    return {};
  },
  mounted() {
    var map = new BMap.Map("map"); // 
    var point = new BMap.Point(113.690612,34.615789); // 
    map.centerAndZoom(point, 18); // 
    map.enableScrollWheelZoom(true); //
    var marker = new BMap.Marker(point); // 
    map.addOverlay(marker); // 
    var opts = {
      width: 50, // 
      height: 10, // 
      title: "" // 
    };
    var infoWindow = new BMap.InfoWindow(
      "",
      opts
    ); // 
    map.openInfoWindow(infoWindow, map.getCenter()); // 
    var wow = new WOW();
    wow.init();
  },
  methods:{
      CommitInfo() {
        console.log("123");
        EMail.sendMail("1231232131")
        alert("");

      }
    }
  
};
</script>
<style scoped>
.banner {
  color: #fff;
  font-size: 30px;
  height: 150px;
  line-height: 150px;
  background-image: url("../assets/img/banner_1.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: scroll;
  background-position: center center;
}
.ContactUs-container {
  padding: 80px 0;
  transition: all ease 0.5s;
  box-sizing: border-box;
}
#map {
  width: 100%;
  height: 365px;
}
.row {
  margin-right: 0;
  margin-left: 0;
}
@media screen and (max-width: 997px) {
  .ContactUs-container {
    padding: 20px 0;
  }
}
</style>




